
.products {
    margin: 20px auto;
    width: 80%;
    display: flex;
    flex-wrap: wrap;
    border-radius: 5px;
}
.card {
    width: 300px;
    height: 400px;
    border: 1px solid black;
    border-radius: 5px ;
    overflow: hidden   ;
    padding: 10px;
    margin: 40px 20px;
    background-color: #f5f5f5;
}
.product-image {
    width: 100%;
    max-width: 300px;
    min-height: 200px;
    display: block;
    object-fit: cover;

}